$main-color: #040404;
$secondary-color: #E6E8E6;
$accent-color: #6A8E7F;
$dark-mode-main-color: #040404;
$dark-mode-secondary-color: #9FA4A9;

body {
  background-color: $secondary-color;
  color: $main-color;
  box-sizing: border-box;
}

:link {
  text-decoration: none;
  color: $secondary-color;

}
.header h2 {
  color: $secondary-color;
}

.header {
  background-image: url("../public/vancouverTransparentskyline.png");
  background-repeat: no-repeat;
  background-size: 100% 80%;
  background-color: $main-color;
  color: $secondary-color;
  height: 400px;
  display: flex;
  align-items: flex-end;

}
.menu-items {
  display: flex;
  width: 100%;
  justify-content: space-around;
}
.header h2:hover {
  color: $accent-color;
}
.home {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  width: 30%;
  margin: auto;
}

.headshot {
  border-radius: 100%;
}
.hamburger {
  display: none;
}
button {
  background-color: $main-color;
}
.about p {
  font-size: 16pt;
  text-align: left;
}
#ryder {
  width: 333px;
  height: 444px;
  margin-right: 50px;
  margin-bottom: 50px;
  border: 2px solid $main-color;
}
#map {
  width: 280px;
  height: 180px;
  margin-left: 50px;
  margin-bottom: 50px;
  align-self: flex-end;
}
.container {
  display: flex;
  margin: 0% 15%;
}
.cvItems {
  display: flex;
  flex-direction: column;
}
.cvItems button {
  width: 100px;
  height: 50px;
  align-self: center; 
}
.cvItems button a {
  color: $secondary-color;
}

.cvItems button a:hover {
  color: $accent-color;
}

iframe {
  align-self: center;
  width:60%;
  height:100vh;
}
.footer a {
  margin: 10px;
}
.footer p {
  margin: 20px;
}
.footer .container p {
  align-self: flex-end;
}
.footer {
  background-color: $main-color;
  color: $secondary-color;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.footer img {
  width: 40px;

}

@media screen and (max-width: 600px) {
  
  .header {
    display: block;
    height: 200px;
  }
  .hidden.menu-items {
    display: flex;
    flex-direction: column;
    background-color: $main-color;
    position: relative;
    bottom: -140px;

  }
  .menu-items {
    display: none;
  }
  .home {
    width: 80%;
  }
  .hamburger {
    display: block;
    position: relative;
    left: 5%;
    bottom: -140px;
  }
  .container {
    display: block;
  }
  #map {
    margin-left: 0px;
  }
  iframe {
    width: 100%;
    height: 100vh;
  }
  
}